<template>
  <div id="LiveTimeList">
    <head-top title="实况录播"></head-top>

    <template v-if="videoItem">
      <t-video ref="video" :src="videoItem.src" :poster="defaultPost"></t-video>
      <div class="video_info">
        <div class="flex-between info_title">
          <span>标签： {{ videoItem["RecLabel"] || '无' }}</span>
        </div>
        <div class="flex-between info_mark">
          <van-tag mark type="success">{{ videoItem.title }}</van-tag>
          <div>{{ videoItem.time | formatDate(videoItem.time) }}</div>
        </div>
      </div>
    </template>

    <van-search v-model="keyWord" placeholder="请输入搜索关键词" />

    <div v-if="restList" class="rest_list">
      <div v-for="(item, index) in restList" :key="index" class="line_mark"
        @click="select(item)">
        <img :src="defaultPost" alt="" />
        <div class="line_right">
          <van-tag mark type="success">{{ item.title }}</van-tag>
          <div class="line_info">标签： {{ item["RecLabel"] || '无' }}</div>
          <div>{{ item.time | formatDate(item.time) }}</div>

        </div>
      </div>
    </div>
    <r-lack-data v-if="!playList || playList.length === 0"></r-lack-data>

    <div class="footer">
      <van-tabbar v-model="activeIndex">
        <van-tabbar-item v-for="(item, index) in menus" :key="index" @click="handleClick(item)">
          <i slot="icon" class=" iconfont" :class="item.icon"></i>
          <span>{{item.name}}</span>
        </van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import defaultBg from "@/assets/video/video.png";
import defaultPost from "@/assets/video/videopost.png";
import tVideo from "@/components/r-video";
import { microLessonTypeName } from "@/utils/enum";

export default {
  name: "",
  components: { tVideo },
  props: {
    routeType: {
      type: String,
      default: "course"
    }
  },
  data() {
    return {
      defaultPost,
      activeIndex: 0,
      menus: [
        { name: "老师实况", index: 0, icon: "icon-pingmu-" },
        { name: "小组实况", index: 1, icon: "icon-renmai" }
      ],
      keyWord: "",
      teacherTypes: [1, 2, 3, 4],
      selectIndex: -1,
      liveList: [],
      videoItem: null,
      playerOptions: {
        autoplay: false,
        muted: true,
        language: "en",
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [
          {
            type: "video/mp4",
            // mp4
            src: "http://vjs.zencdn.net/v/oceans.mp4"
            // webm
            // src: "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm"
          }
        ],
        poster:
          "https://surmon-china.github.io/vue-quill-editor/static/images/surmon-1.jpg"
      },
      loading: false
    };
  },
  computed: {
    playList() {
      return this.liveList
        .filter(
          r =>
            (this.activeIndex === 0 &&
              this.teacherTypes.indexOf(r["MLType"]) > -1) ||
            (this.activeIndex === 1 &&
              this.teacherTypes.indexOf(r["MLType"]) === -1)
        )
        .filter(
          r =>
            (this.keyWord && r.title.indexOf(this.keyWord) > -1) ||
            r["RecLabel"].indexOf(this.keyWord) > -1 ||
            !this.keyWord
        );
    },
    // videoItem() {
    //   return this.playList && this.playList.length > 0
    //     ? this.playList[this.selectIndex]
    //     : null;
    // },
    restList() {
      return this.playList.filter(r => r.index !== this.selectIndex);
    }
  },
  created() {
    this.$store.commit("SET_LIVELESSION", null);
    this.getList();
  },
  methods: {
    getList() {
      this.dataList = [];
      this.liveList = [];
      this.loading = true;
      if (this.$config.isTest) {
      } else {
        this.$mainHttp.miniCourse
          .getList({
            ciID: this.$store.getters.courseID // "20010314124042597100499003575071" 
          })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach((item, index) => {
              let sub = {
                  ...item,
                  index,
                  title: item["MLTitle"],
                  time: item["StartTime"],
                  typeName: microLessonTypeName(item["MLType"]),
                  src: this.routeType === "course" ? this.$gatewayHttp.resourse.video(item["MLID"], item["FileFormat"])
                        : this.$mainHttp.resourse.video(item["MLID"], item["FileFormat"]),
                  poster: defaultBg
                };
                this.liveList.push(sub);
              });
            }
            this.loading = false;
          })
          .catch(err => {
            this.loading = false;
          });
      }
    },
    handleClick(item) {
      this.keyWord = "";
      this.selectIndex = -1;
      this.videoItem = null;
      this.activeIndex = item.index;
    },
    select(item) {
      this.selectIndex = item.index;
      this.videoItem =
        this.liveList && this.liveList.length > 0
          ? this.liveList[this.selectIndex]
          : null;
      // this.videoItem.src = "http://v1.ergengtech.com/transcode/4942b6557a1cb960fb855da01947d62f/e64489d74762dc1ce536e545a5de4182.mp4";
      if (this.$refs["video"]) {
        this.$refs["video"].stopPlay();
        this.$refs["video"].init();
      }
      setTimeout(() => {
        this.$refs["video"].clickPlayBtn();
        window.scrollTo(0, 0);
      }, 200);
    }
  }
};
</script>

<style lang="less">
@import url("./../../styles/common/base.less");
#LiveTimeList {
  width: 100%;
  height: 100%;
  margin-bottom: 50px;
  background-color: #fff;

  .video_info {
    text-align: left;
    font-size: @font-info;
    line-height: 1.5;
    padding: 2vw 3vw;

    .info_title {
      word-break: break-all;
      word-wrap: break-word;
    }

    .info_mark {
      margin-top: 1vw;
    }
  }

  .video-player {
    width: 100vw;
    min-height: 55vw;
    margin: 0 auto;
    position: relative;
    overflow: hidden;

    video {
      width: 100%;
      margin: 0 auto;
    }
  }

  .rest_list {
    padding-top: 2vw;
    border-top: 1px solid #f1f1f1;
    .line_mark {
      position: relative;
      display: flex;
      justify-content: start;
      margin: 0 3vw;
      font-size: @font-info;
      line-height: 1.5;
      color: #777;
      padding: 2vw 0;
      &:after {
        content: "";
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 1px;
        border-top: 1px solid #f0f1f1;
        transform: scaleY(0.6);
      }

      img {
        max-width: 26vw;
        max-height: 20vw;
        padding-right: 4vw;
      }
      .line_right {
        text-align: left;
        word-break: break-all;
        margin-top: 1.5vw;
        .line_info {
          line-height: 30px;
        }
      }
    }
  }
  .footer {
    /deep/ .van-tabbar-item--active {
      color: #00cbab;
    }
  }
}
</style>



